.card__stars {
  display: flex;
  justify-content: flex-start;
}

.card__star {
  background-image: url("../images/star.svg");
  background-repeat: no-repeat;
  background-position: center;
  list-style-type: none;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__stars--1 > .card__star:first-child,
.card__stars--2 > .card__star:nth-child(-n+2),
.card__stars--3 > .card__star:nth-child(-n+3),
.card__stars--4 > .card__star:nth-child(-n+4),
.card__stars--5 > .card__star:nth-child(n) {
  background-image: url("../images/star-active.svg");
}
